*{
  margin: 0;
  padding: 0;
}
.bona-nova-sc-regular {
  font-family: "Bona Nova SC", serif;
  font-weight: 400;
  font-style: normal;
}

.bona-nova-sc-bold {
  font-family: "Bona Nova SC", serif;
  font-weight: 700;
  font-style: normal;
}

.bona-nova-sc-regular-italic {
  font-family: "Bona Nova SC", serif;
  font-weight: 400;
  font-style: italic;
}
.link{
  text-decoration: none;
  border: 2px solid goldenrod;
  border-radius: 7px;
  padding: 10px;
  color: goldenrod;
  background-color: #fff;
}
.link:hover{
  color: #000;
}
/* //////////////////////////////////////////////////////////////////////////////////////// */
.navbar
{
  background-image: linear-gradient(43deg,#000 0%, #333 60%, #000 160%);
  width: 100%;
  display: flex;
  justify-content: center;
  padding:20px;
}
.navbrand{
  font-weight: bold;
  font-size: 30px;
}
.navLink{
  color: goldenrod;
  font-weight: bold;
  border: 2px solid goldenrod;
  border-radius: 7px;
  padding: 5px;
}
.navLink:hover{
  background-image: linear-gradient(43deg,goldenrod 0%, #222 60%,goldenrod 120%);
  color: #fff;
}
.banner{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  /* align-items: center; */
  height: 700px;
  width: 100%;
  margin: auto;
 background-image:  linear-gradient(43deg,#000 0%, #333 60%,#000 -120%);
}
.banner_image_div{
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.imageContainer{
  display: flex;
  justify-content: center;
  width: 30%;
  border-radius: 10px;
}
.My_image{
  width: 30vw;
  border-radius: 50%;
}
.name{
  font-weight: bold;
  width: 30vw;
  background-image:  linear-gradient(43deg,#aaa 0%, rgb(227, 175, 43) 60%,#fff 120%) ;
  -webkit-background-clip: text;
  color: transparent;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  border-radius: 10px;
}
/* /////////////////////////////// SERVICES */
.banner_2{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: auto;
  width: 100%;
  margin: auto;
 background-image:  linear-gradient(43deg,#000 0%, #333 -60%,#000 120%);
}
.heading{
  display: flex;
  justify-content: center;
  color: goldenrod;
}
.services{
  width: 100%;
  padding:  20px ;
  display: grid;
  grid-template-columns: 30% 30% 30%;
  justify-content: center;
  align-items: center;

}
.services_card{
  justify-self: center;
}
.services_card:hover{
  transform: scale(1.3);
  z-index: 2;
  transition: all 0.5s ease-in-out;
  box-shadow: 5px 5px 10px 5px #333;

}
.projectContainer{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  gap: 40px;
}
.project_card
{
    width:80% ;
    display: flex;
    flex-direction: row;
    gap: 20px;
    color: #aaa;
    background-image:  linear-gradient(43deg,#000 0%, #333 60%,#000 -120%);
    padding: 20px;
}
.project_card:hover
{
  transform: scale(1.1);
  z-index: 2;
  transition: all 0.5s ease-in-out;
  
}
.flickbase{
  flex-direction: row-reverse;
}
.card_image{
  width: 50%;
}
.card_body{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* //////////////////////////////////// EDUCATION/////////////////////////////////// */

.education{
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 20px;
  color: #fff;

}
.education_list{
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.certification{
  width: 100%;
  padding:20px;
  margin-top: 10px;
}

.certy{
  width: 100%;
  display: grid;
  grid-template-columns: 40% 40%;
  align-items: center;
  justify-content: center;
  row-gap: 20px;
}
.certy_card{
  justify-self: center;
  padding: 10px;
  width: 70%;
  color: goldenrod;
  background-image: linear-gradient(43deg,#000 0%, #333 60%,#000 -120%);
}
.certy_images{
  width: 100%;
}

/* ////////////FOOTER ////////////////////// */
.main_footer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(43deg,#333 0%,#555 60%,#000 -120%) ;
}
.main_footer>p{
  color: goldenrod;
}
.footer{
  width: 100%;
  height: 300px;
  background-image: linear-gradient(43deg,#333 0%,#555 60%,#000 -120%) ;
  display: flex;
  justify-content:center;
  gap: 20px;
  align-items: center;
}
.feather
{
  fill: goldenrod;
  height: 50px;
}
.social_url{
  display:flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
}

/* ////////////////Projects///////////////////// */

.projects{
 padding: 20px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  background-image: linear-gradient(43deg,#000 0%,#333 60%,#000 -120%);
}
.projects_section_card{
  display: flex;
  flex-direction: row;
  width: 80%;
  background-image: linear-gradient(43deg,#000 0%,#333 60%,#000 -120%);
  color: #aaa;
  box-shadow: 5px 5px 10px 5px #333;
  padding: 10px;

}
.projects_section_card_image{
  width: 50%;
}
.projects_section_card_body{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.beyond{
  background-image:  linear-gradient(43deg,#000 0%,#333 60%,#000 -120%);
  padding: 20px;
}
.description{
  width: 100%;
  display: grid;
  grid-template-columns: 40% 40%;
  justify-content: center;
  align-items: center;
}
.beyond_cards{
  justify-self: center;
  width: 90%;
  padding: 20px;
  background-image:  linear-gradient(43deg,#000 0%,#333 60%,#000 -120%);
  color: #aaa;
}
.beyond_para{
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: goldenrod;
}

/* ///////////////////////// MEDIA QUERIES //////////////////////////// */

@media only screen and (max-width: 1170px) {
  .services{
    display: grid;
    grid-template-columns: 40% 40%;
    row-gap: 20px;
  }
  .projects_section_card{
    flex-direction: column;
  }
  .projects_section_card_image{
    width: 100%;
  }
  
}

@media only screen and (max-width: 915px) {
  .services{
    display: grid;
    grid-template-columns: 100%;
    row-gap: 20px;
  }
  .services_card{
    width: 100%;
  }
  .My_image{
    width: 50vw;
  }
  .certy{
    grid-template-columns: 100%;

  }
  .certy_card{
    width: 100%;
  }
  .footer{
    display: grid;
    grid-template-columns: 40% 40%;
    justify-content: center;
    align-items: center;
    gap: 0px;
  }
  
}
@media only screen and (max-width: 750px) {
  .project_card{
    flex-direction: column;
    font-size: 15px;
  }
  .card_image{
    width: 100%;
  }
  .description{
    width: 100%;
    grid-template-columns: 100%;
    row-gap: 20px;
  }
  
}
@media only screen and (max-width: 550px) {
  .navbar{
    font-size: 15px;
    padding: 10px;
  }
  .navbrand{
    display: none;

  }
  .navLink{
    padding: 1px;
  }
  .banner_image_div{
    flex-direction: column;
    width: 100%;
    margin:auto;
  }
  .name{
    width: 100%;
    margin: auto;
    justify-content: center;
    align-items: center;
  }
  .name>p{
    width: 80%;
    margin:auto;
    display: flex;
    justify-content: center;
  }
  .services{
    padding: 0px;
  }
  .services_card{
    box-shadow: 3px 3px 7px 3px #333;
    font-size: 12px;
    width: 90% !important;
  }
  .services_card:hover{
    transform: scale(1);
    z-index: 1;
  }
  .footer{
    display: grid;
    grid-template-columns: 100%;
    justify-content: center;
    align-items: center;
    gap: 0px;
  }
  .projects{
    padding: 20px 0px ;
  }
  .projects_section_card{
    font-size: 12px;
  }
  .projectContainer{
    /* width: 100%; */
    padding: 0px;
  }
  .beyond_cards{
    font-size: 12px;
  }
  .beyond_para{
    padding: 5px;
    gap: 10px;
  }
  .education{
    width: 100%;
  }
  
}